.button {
  border-radius: 5px;
  display: flex;
  flex-direction: row;

  cursor: pointer;
  font-family: inherit;

  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.3;
  justify-content: center;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  transition: var(--transition-duration);
  transition-timing-function: var(--timing-function);
}
.button:hover {
  background-color: #ab8d7852;
}

.button-group {
  display: flex;
  text-align: center;
  justify-content: center;
  @media screen and (max-width: 767px) {
    margin-top: 30px;
  }
  
}

.library__button {
  // margin-bottom: 62px;
  // margin-top: 52px;
  width: 130px;
  height: 44px;
  background: transparent;
  color: var(--secondary-text-color);
  border: solid 1px;
  border-color: var(--secondary-text-color);

  @media screen and (min-width: $tablet) {
    width: 152px;
  }
  @media screen and (min-width: $desktop) {
    width: 147px;
    height: 42px;
  }
}

.library__button:not(:last-child) {
  margin-right: 20px;

  @media screen and (min-width: $tablet) {
    margin-right: 32px;
  }
  @media screen and (min-width: $desktop) {
    margin-right: 16px;
  }
}

.library__button.is-active {
  background-color: var(--accent-color);
  border-color: var(--accent-color);
}
